import React, { Component } from 'react';

class ThankYou extends Component {
  static async getInitialProps({asPath}) {
    return {asPath}
  }
  render() {
    return (
      <h1>Thank you! {this.props.asPath}</h1>
    );
  }
}

export default ThankYou;



